import { Api } from '../base/api';
import { localArmy } from '../../types/warriorsData';
import { localWeapons, localWeaponsWheels } from '../../types/weaponsData';
import { localfightMachine } from '../../types/fightMachineData';
export class WebLarekAPI extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    // Функция для обработки путей изображений в description
    getWarriorsItem(id) {
        const item = localArmy.find((i) => i.id === id);
        if (!item) {
            return Promise.reject('Item not found');
        }
        const descriptionHtml = item.description.startsWith('http') ||
            item.description.startsWith('https')
            ? item.description
            : `${process.env.PUBLIC_URL}${item.description}`;
        // Обрабатываем описание с картинками
        const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
            ? item.image
            : `${process.env.PUBLIC_URL}${item.image}`;
        return Promise.resolve(Object.assign(Object.assign({}, item), { type: 'list', image: imageUrl, description: descriptionHtml }));
    }
    getWeaponsItem(id) {
        const item = localWeapons.find((i) => i.id === id);
        if (!item) {
            return Promise.reject('Item not found');
        }
        // Обрабатываем описание с картинками
        const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
            ? item.image
            : `${process.env.PUBLIC_URL}${item.image}`;
        return Promise.resolve(Object.assign(Object.assign({ type: 'tech' }, item), { image: imageUrl }));
    }
    getWeaponsWheelsItem(id) {
        const item = localWeaponsWheels.find((i) => i.id === id);
        if (!item) {
            return Promise.reject('Item not found');
        }
        // Обрабатываем описание с картинками
        const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
            ? item.image
            : `${process.env.PUBLIC_URL}${item.image}`;
        return Promise.resolve(Object.assign(Object.assign({ type: 'wheels' }, item), { wheelsPrice: 10, isWheels: false, image: imageUrl }));
    }
    getFightingMachineItem(id) {
        const item = localfightMachine.find((i) => i.id === id);
        if (!item) {
            return Promise.reject('Item not found');
        }
        // Обрабатываем описание с картинками
        const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
            ? item.image
            : `${process.env.PUBLIC_URL}${item.image}`;
        return Promise.resolve(Object.assign(Object.assign({ type: 'machine' }, item), { weapons: item.weapons.map((weapon) => (Object.assign(Object.assign({}, weapon), { quantity: 0 }))), image: imageUrl }));
    }
    getWarriorsList() {
        return Promise.resolve(localArmy.map((item) => {
            const descriptionHtml = item.description.startsWith('http') ||
                item.description.startsWith('https')
                ? `<img src="${item.description}" alt="Description Image">`
                : item.description;
            const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
                ? item.image
                : `${process.env.PUBLIC_URL}${item.image}`;
            return Object.assign(Object.assign({}, item), { type: 'list', image: imageUrl, description: descriptionHtml });
        }));
    }
    getWeaponsList() {
        return Promise.resolve(localWeapons.map((item) => {
            const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
                ? item.image
                : `${process.env.PUBLIC_URL}${item.image}`;
            return Object.assign(Object.assign({}, item), { type: 'tech', image: imageUrl });
        }));
    }
    getWeaponsWheelsList() {
        return Promise.resolve(localWeaponsWheels.map((item) => {
            const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
                ? item.image
                : `${process.env.PUBLIC_URL}${item.image}`;
            return Object.assign(Object.assign({}, item), { type: 'wheels', wheelsPrice: 10, image: imageUrl });
        }));
    }
    getFightingMachineList() {
        return Promise.resolve(localfightMachine.map((item) => {
            const imageUrl = item.image.startsWith('http') || item.image.startsWith('https')
                ? item.image
                : `${process.env.PUBLIC_URL}${item.image}`;
            return Object.assign(Object.assign({ type: 'machine' }, item), { weapons: item.weapons.map((weapon) => (Object.assign(Object.assign({}, weapon), { quantity: 0 }))), image: imageUrl });
        }));
    }
    orderCards(order) {
        return this.post('/order', order).then((data) => data);
    }
}
