import { Component } from '../base/Component';
import { EventEmitter } from '../base/events';
import { ensureElement } from '../../utils/utils';
export class Weapon extends Component {
    constructor(container, actions) {
        super(container, new EventEmitter());
        this._title = ensureElement('.weapon_title', container);
        this._increase = ensureElement('.increase', container);
        this._decrease = ensureElement('.decrease', container);
        this._quantity = ensureElement('.weapon_number', container);
        if (actions.increase) {
            this._increase.addEventListener('click', () => {
                if (!this.isMax) {
                    actions.increase();
                }
            });
        }
        if (actions.decrease) {
            this._decrease.addEventListener('click', () => {
                if (this.quantityValue > 0) {
                    actions.decrease();
                }
            });
        }
    }
    renderTitle(title, price) {
        this.setText(this._title, `${title} [${price}]`);
    }
    set quantity(value) {
        this.quantityValue = value;
        this.setText(this._quantity, String(value));
    }
    renderButtons() {
        this._increase.disabled = this.isMax;
        this._decrease.disabled = this.quantityValue < 1;
    }
    render(data) {
        const element = super.render(data);
        this.renderTitle(data.title, data.price);
        this.renderButtons();
        return element;
    }
}
