import { ensureElement } from '../../utils/utils';
import { Component } from '../base/Component';
export class Page extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._counter = ensureElement('.header__basket-counter', container);
        this._catalog = ensureElement('.gallery', container);
        this._wrapper = ensureElement('.page__wrapper', container);
        this._basket = ensureElement('.header__basket', container);
        this._modal = ensureElement('.modal__container', container);
        // обработчик клика на корзину
        this._basket.addEventListener('click', () => {
            this.events.emit('basket:open');
        });
    }
    //Установка значения счетчика корзины.
    set counter(value) {
        this.setText(this._counter, String(value));
    }
    set catalog(items) {
        this._catalog.replaceChildren(...items);
    }
    set locked(value) {
        this.toggleClass(this._wrapper, 'page__wrapper_locked', value);
        this.toggleClass(this._modal, 'modal__container_locked', value);
    }
}
